.header {
  width: 100%;
  border-bottom: 1px solid $lipstick;
  padding: 30px 0;
  background: $black;
  @include r(767) {
    padding-left: 70px;
    padding-right: 20px; }
  @include r(360) {
    padding-left: 30px; }
  .menu-open {
    width: 40px;
    height: 30px;
    padding-top: 15px;
    position: absolute;
    display: inline-block;
    cursor: pointer;
    top: 0px;
    left: -40px;
    transition: all 0.3s;
    display: none;
    @include r(767) {
      display: block; }
    @include r(360) {
      left: -20px; }
    span {
      width: 25px;
      height: 4px;
      background: $white;
      display: block;
      transition: all 0.3s;
      border-radius: 20%;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        width: 25px;
        height: 4px;
        background: $white;
        transition: all 0.3s;
        top: 6px;
        border-radius: 20%; }
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 2px;
        width: 25px;
        height: 4px;
        background: $white;
        transition: all 0.3s;
        border-radius: 20%; } } }
  &__block {
    display: flex;
    @include r(1024) {
      justify-content: center; }
    @include r(767) {
      justify-content: flex-start; }
    &__logo {
      @include r(1024) {
        margin-bottom: 40px; }
      @include r(767) {
        margin-bottom: 0; }
      h2 {
        font-family: 'Informal011 BT';
        color: $lipstick-two;
        font-size: 37.5px;
        font-weight: 900;
        line-height: 0.98;
        letter-spacing: 0.9px;
        position: relative;
        display: inline-block;
        @include r(767) {
          font-size: 28px; }
        span {
          color: $white; }
        p {
          font-size: 9.6px;
          font-weight: 500;
          color: $pinkish-grey-three;
          letter-spacing: 0.2px;
          position: absolute;
          right: 5px;
          @include r(767) {
            font-size: 7px; } } } }
    &__phone {
      display: flex;
      flex-direction: column;
      position: relative;
      padding: 0 45px;
      padding-right: 25px;
      border-right: 1px solid $lipstick;
      @include r(767) {
        display: none; }
      &--mobile {
        display: none;
        i {
          color: $lipstick-two;
          font-size: 26px; }
        @include r(767) {
          display: block; } }

      i {
        position: absolute;
        color: $lipstick-two;
        font-size: 26px;
        left: -5px;
        top: 7px; }
      a {
        font-weight: normal;
        color: $gunmetal;
        &:hover {
          color: $lipstick-two; } }
      a:nth-child(2) {
        font-size: 16px;
        font-weight: 500;
        color: $silver;
        padding-bottom: 5px;
        &:hover {
          color: $lipstick-two; } } }
    &__location {
      display: flex;
      flex-direction: column;
      position: relative;
      padding-left: 70px;
      @include r(767) {
        display: none; }
      i {
        position: absolute;
        color: $lipstick-two;
        font-size: 28px;
        left: 30px;
        top: 7px; }
      a {
        font-size: 16px;
        color: $cool-grey;
        &:hover {
          color: $lipstick-two;
          p {
            color: $lipstick-two; } }
        p {
          color: $gunmetal;
          padding-top: 5px;
          transition: all 0.3s; } } }
    &__btn {
      @include r(767) {
        display: none; }
      a {
        padding: 15px 17px;
        background: $lipstick;
        display: inline-block;
        color: $white;
        font-size: 15px;
        line-height: 1.07;
        border: 1px solid $lipstick-two;
        @include r(1130) {
          width: 165px;
          padding: 15px 10px; }
        &:hover {
          color: $lipstick-two;
          background: none;
          i {
            color: $lipstick-two; } }
        i {
          padding-right: 10px; } } } } }
.header-toolbar {
  &--mobile {
    display: none;
    position: absolute;
    left: 85px;
    @include r(767) {
      display: block; }
    @include r(440) {
      left: 30px; } }
  &__phone {
    padding: 12px 0;
    a {
      font-size: 14px;
      color: $white;
      i {
        color: $lipstick-two;
        font-size: 15px;
        padding-right: 10px; } } }
  &__location {
    a {
      font-size: 14px;
      color: $white;
      i {
        color: $lipstick-two;
        font-size: 15px;
        padding-right: 10px; } } }
  nav {
    position: absolute;
    @include r(767) {
      display: none; }
    li {
      display: inline-block; }
    a {
      color: $white;
      font-size: 16px;
      font-weight: 500;
      padding: 20px 0;
      padding-right: 50px;
      display: inline-block;
      text-transform: uppercase;
      &:hover {
        color: $lipstick-two; } } } }
